import React from "react"
import { makeStyles } from "@material-ui/styles"
import { Theme } from "@material-ui/core/styles"
import { SlugsInfo } from "../../../generated/ProjectPerYear"
import NavAndSideBarLayout from "../../layout/NavAndSideBarLayout"
import {
  Button,
  Card,
  CardActions,
  CardContent,
  Grid,
  Typography,
} from "@material-ui/core"
import { Link } from "gatsby"

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    display: "flex",
  },
  link: {
    color: "inherit",
    textDecoration: "none",
  },
}))

interface Props {
  pageContext: PageContext
}

interface PageContext {
  slug: string
  slugsInfos: SlugsInfo[]
}

const Template404: React.FC<Props> = ({ pageContext }: Props) => {
  const classes = useStyles()
  const redirectRoute = pageContext.slugsInfos.sort(
    (a: SlugsInfo, b: SlugsInfo) => b.year - a.year,
  )[0].slug

  return (
    <NavAndSideBarLayout
      slugsInfos={pageContext.slugsInfos}
      currentSlug={pageContext.slug}
    >
      <Grid container className={classes.root} justify="center">
        <Grid item lg={10}>
          <Card>
            <CardContent>
              <Typography variant={"h5"} component={"h2"}>
                404 Page not found
              </Typography>
              <Typography variant={"body1"} paragraph>
                The page your trying to visit does not exists.
              </Typography>
            </CardContent>
            <CardActions>
              <Link to={redirectRoute} className={classes.link}>
                <Button size="small" color="primary">
                  Go to the index page
                </Button>
              </Link>
            </CardActions>
          </Card>
        </Grid>
      </Grid>
    </NavAndSideBarLayout>
  )
}

export default Template404
